<script>
  //contentType 1 video   2 live  3book  4 pic
  import {mapActions} from 'vuex' 

  let Base64 = require('js-base64').Base64

  let nologins=["/photo","/book","/about","/help","/login"]
  
  export default {
    name:'home',

    data() {
      return {
        swiperOption: {
      autoplay: 2000,
      setWrapperSize :true,
      centeredSlides: true,
      autoHeight:true,
    },
    bannerlist:["http://imagess-google.com/ctx/mobile/banner/banner1.png?d762","http://imagess-google.com/ctx/mobile/banner/banner2.png?d762","http://imagess-google.com/ctx/mobile/banner/banner3.png?d762","http://imagess-google.com/ctx/mobile/banner/banner4.png?d762"],
       dialog:false,
       canload:true,
       open:false,
       docked:true,
       loading:false,
       scroller: null,
       list:[],
       page:1,
       apitype:"movies",
       title:"Movies",
       contentType:3,
       screenWidth:0,
       bottomNav: '3',
       bottomNavColor: '1',
       apiurl:"/api/movies?tp=4K-STAR&page=",
       label:'Beautyleg',
      kw:'',
       cate:'性感',
     }
   
   },
   mounted() {
    
     this.scroller=window
     this.screenWidth=window.width
     this.dialog=this.$store.state.showTip   
     this.loadMore()

   },
   computed:{
    msg(){
      return this.$store.state.test
    },
    logined(){
      return this.$store.state.loginedUser
    },
    livekeys(){
      return this.$store.state.livekeys
    },
    photokeys(){
      return this.$store.state.photokeys
    },
    bookkeys(){
      return this.$store.state.bookkeys
    },
    videokeys(){
      return this.$store.state.videokeys
    }
  },
  beforeRouteEnter(to,from,next)
    {
      next(vm=>{
        vm.canload=true
      })
    },
  beforeRouteLeave(to,from,next)
  { 

    return next(true)
    
    if(nologins.indexOf(to.path)>0){
      next(true)
      return
    }
    
    if(this.logined){
      next(true)
      this.canload=false
      return
    }

    if(this.canView()){
      next(true)
      this.canload=false
    }
    else {
       this.$router.push({path:"/login"})
       alert("您已經過了體驗期，請註冊！")
    }
   
   
},

methods: {

  PrefixInteger(num, length) {
    return (Array(length).join('0') + num).slice(-length);
},

  canView() {
    if(!localStorage.getItem("canView")){
      return false
    }
    return localStorage.getItem("canView")=="true"
  }
  ,
  toggle (flag) {
    this.open=!this.open
    this.docked=!flag

  },
  handleChange (val) {
      this.bottomNav = val
      this.itemclick(val)
  },
  loadMore(added=true){
    if(this.currentType==1)return;
    if(!this.canload){
      return 
    }
    if(!added){
      this.list=[]
    }
    this.loading=true
    this.$http.get(this.$store.state.serverUrl+this.apiurl+this.page,{headers:{"Authorization":"test"}}).then(response=>{
     let result=JSON.parse(Base64.decode(response.body))

     if (result.length==0){
       this.canload=false;
       return;
     }

     if(added){
     
        this.list=this.list.concat(result)
      
    }
    else {
      this.list=result

    }
    this.loading=false
    this.page++
  },response=>{
    this.list=[]
  })
  },
  closeTip(){
    this.dialog=false
    this.open=false
  },
  gotobook(item){
   
    
    let pages=item.pages
    let pics=[]
    for(var i =1;i<pages;i++){
      pics.push(item.imgurl+this.PrefixInteger(i,3)+".jpg")
    }
    this.changebook({title:item.title,images:pics})
    this.$router.push({path:'/book'})
  },
  gotoVipVideo(item)
  {
    this.changeVipVideo(item)
    this.$router.push({path:"/vipvideo"})
  },
  gotoProfile()
  {  
    if(this.$store.loginedUser){
      this.$router.push({path:"/profile"})
      }
    else {
      this.$router.push({path:"/login"})
    }
  },
    
    
  gotoRoute(pa)
  {

    this.$router.push({path:pa })
  },
  gotomm(item){
   let id=item.id
   let pages=item.page
   let pics=[]
   for (var i=1;i<=pages;i++){
    pics.push("http://img1.mm131.com/pic/"+id+"/"+i+".jpg")

  }
  this.changemm({title:item.title,images:pics})
  this.$router.push({path:'/picture'})
},
goto942Video(item)
{   
    let da={}
    da['poster']='https://www.942porn.com/media/videos/tmb/'+item.sid+"/1.jpg"
    let pics=[]
    for(var i =1;i<20;i++){
      pics.push('https://www.942porn.com/media/videos/tmb/'+item.sid+"/"+i+".jpg")
    }
    da['photos']=pics
    da['mp4']="https:"+item.url
    da['title']=item.title
    this.changevideo(da);
  this.$router.push({path:"/video"})

},
updateUrl(k)
{
  this.apiurl=k['url']
  this.cate=k['tag']
  this.page=1
  this.canload=true
  this.loadMore(false)  
},

updateCate(item)
{
  this.canload=true
  this.label=item
  this.page=1
  this.loadMore(false)
},
updateKeyWord(item,index)
{
 this.page=1
 this.kw=item
 this.cate=index
 this.canload=true
 this.loadMore(false)
},
gotoLiveVideo(item)
{ 
  
  this.changeLiveVideo(item)
  this.$router.push({path:"/livevideo"})
},
gotosvideo(item)
{
   item['photos']=[]
   this.changevideo(item);
   this.$router.push({path:"/video"})

},
gotovideo(item)
{ 

  if(item['poster'].indexOf("dmm.com")>0){
   let size=item['photonum']
   let pics=[]
   for (var i = 1; i <=size; i++) {
    pics.push({"url":"https://pics.dmm.com/digital/video/"+item['movienumber']+"/"+item['movienumber']+"jp-"+i+".jpg"})
  }
  item['poster']="https://pics.dmm.com/digital/video/"+item['movienumber']+"/"+item['movienumber']+"ps.jpg"
  item['photos']=pics
  
}
this.changevideo(item);
this.$router.push({path:"/video"})
},
...mapActions(['changevideo','changemm','changebook','changeVipVideo','changeLiveVideo','changeUser']),
refresh () {
  this.refreshing=true
  setTimeout(()=>{this.refreshing=false},5000)
},
tologin(){
  this.$router.push({path:"/login"})
},
loginout()
{
  localStorage.removeItem("user")
  localStorage.setItem('canView',false);
  this.changeUser(null)
  alert("您已成功推出當前賬號")
  window.location="/"
},
itemclick(tp){
  this.list=[]
  if(tp==1){
    //this.apiurl=this.photokeys[0]['url']
    this.title="彩天下"
    this.contentType=1;
  }
  else if(tp==2){
    this.apiurl=this.bookkeys[0]['url']
    this.title="Books"
    this.contentType=2;
  }
  else if(tp==3){
    this.apiurl=this.videokeys[0]['url']
    this.title="Movies"
    this.contentType=3;
  }
  else if(tp==4)
  {
    this.apiurl=this.livekeys[0]['url']
    this.title="Lives"
    this.cate="中文視頻"
    this.contentType=4;
  }
  
 
  this.page=1

  this.loadMore(false)
},
currentType(tp){
  return this.apitype.indexOf(tp)>0
}
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .fixed {
    position:fixed;
